import DatePicker from "components/DatePicker/DatePicker";
import React from "react";
import { getLocalStorage, parsedDateFromAPIValue } from "utils/CommonUtil";
import { LOCAL_STORAGE } from "utils/Constant";

const SystemDatePicker = (props: any) => {
  const maxDate = parsedDateFromAPIValue(getLocalStorage(LOCAL_STORAGE.SYSTEM_DATE));
  return (
    <div>
      
      <DatePicker maxDate={maxDate} viewDate={!props?.value? maxDate:undefined} {...props} />
    </div>
  );
};

export default SystemDatePicker;
